.carousel  {
    padding-top: 20px;
    background-color: #787878; /* Make sure content can overflow if necessary */
}

.carousel .slide {
    display: flex;             /* Enable flexbox */
    justify-content: center;   /* Center horizontally */
    align-items: center;       /* Center vertically */
    height: 100%;              /* Ensure the slide has a full height to center vertically */
}


.carousel .slide {
    display: flex;             /* Enable flexbox */
    justify-content: center;   /* Center content horizontally */
    align-items: center;       /* Center content vertically */
    height: 100%;              /* Make sure the slide uses full height */
}

.carousel .slide img {
    max-width: 100%;           /* Ensure the image doesn't overflow horizontally */
    max-height: 100%;          /* Ensure the image doesn't overflow vertically */
    object-fit: contain;       /* Make the image fit within its container */
}
